<template>
  <div class="info" v-if="percentage">
    <span>{{percentage}}</span><br/> {{description}}
  </div>
</template>
<script>
export default {
  props: {
    root: {
      required: false,
      type: Object
    },
    current: {
      required: false,
      type: Object
    },
    description: {
      required: true,
      type: String
    }
  },
  computed: {
    percentage() {
      if (this.current == null || this.root == null) {
        return null;
      }

      return `${(100 * this.current.value / this.root.value).toPrecision(3)} %`;
    }
  }
};
</script>
<style lang="less" scoped>
.info {
  width: 300px;
  height: 100px;
  padding: 20px;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 10;
  margin-left: -150px;
  margin-top: -70px;

  span {
    font-size: 2em;
  }
}
</style>
